import Footer from './Footer';
import Header from './header/Header';
import HeadCom from './HeadCom';
import styled from 'styled-components';
import Media from './media';

const DivContentWraper = styled.div`
  text-align: left;
`;

const DivContentWraperL2 = styled.div`
  padding-left:10%;
  padding-right:10%;
  ${Media.sm`padding-right: 5px !important; padding-left: 5px !important;`};
`;

const ContainerI = styled.div`
  width: 100%;
  /* max-width: 1100px !important; */
  background-color: #f4f4f4;
`;

const ColI = styled.div`
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 15px !important;
  padding-left: 15px !important;
  ${Media.sm`padding-right: 5px !important; padding-left: 5px !important;`};
`;
const Bound = styled.div`
  display: flex;
  flex-direction: column;
`;

const layoutStyle = {
  display: 'flex',
  border: '1px solid #DDD',
  height: '100%',
  alignItems: 'center',
  justifyContent: 'center'
};

export default function Layout(props) {
  return (
    <DivContentWraper>
      <DivContentWraperL2>
       <Header/>
        <ContainerI fluid>
          <ColI md="12" sm="12">
            <HeadCom />
            {props.children}
            <Footer />
          </ColI>
        </ContainerI>
      </DivContentWraperL2>
    </DivContentWraper>
  );
}
